.one-col > .row > .col {
  @extend .col-xs-12;
  @extend .col-sm-12;
  @extend .col-md-12;
}

.two-col > .row > .col {
  @extend .col-xs-12;
  @extend .col-sm-6;
  @extend .col-md-6;
}

.three-col > .row > .col {
  @extend .col-xs-12;
  @extend .col-sm-6;
  @extend .col-md-4;
}

.four-col > .row > .col {
  @extend .col-xs-12;
  // 4 has to be divisible by 2 or 1 for this layout to work;
  // 12 columns / col-sm-4 = 3, leaving one odd column orphaned in sm screen size
  @extend .col-sm-3;
  @extend .col-md-3;
}

.six-col > .row > .col {
  @extend .col-xs-12;
  @extend .col-sm-4;
  // same here if col-md-3 is used here the will be 2 orphans
  // 12/3 = 4 ... 6-4 = 2 orphans
  @extend .col-md-2;
  @extend .col-lg-2;
}

